<template>
  <!-- 页面顶部图片 -->
  <div :class="classes" :style="styles">
    <div :class="descClasses">
      <div :class="descHeaderClasses">
        <slot name="header"></slot>
      </div>
      <p :class="descContentClasses">
        <slot></slot>
      </p>  
    </div>
  </div>
</template>

<script>
  const prefixCls = 'gtu-banner'

  export default {
    name: 'banner',
    props: {
      height: {
        type: Number,
        default: 600
      },
      bg: {
        type: String,
        validator (value) {
          return /\.(png|gif|jpe?g|svg)(\?.*)?$/.test(value)
        }
      }
    },
    data () {
      return {}
    },
    computed: {
      classes () {
        return [`${prefixCls}`]
      },
      styles () {
        return {
          height: `${this.height}px`,
          backgroundImage: this.bg ? `url(${this.bg})` : 'none'
        }
      },
      descClasses () {
        return [`${prefixCls}-desc`]
      },
      descHeaderClasses () {
        return [`${prefixCls}-desc-header`]
      },
      descContentClasses () {
        return [`${prefixCls}-desc-content`]
      }
    },
    methods: {}
  }
</script>

<style lang="scss">
  $prefixCls : '.gtu-banner';
  #{$prefixCls} {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #fff;
    background-size: 100% 100%;
    &-desc-header {
      font-size: 42px;
      margin-bottom: 20px;
    }
    &-desc-content {
      font-size: 24px;
      line-height: 1.5;
    }
  }
</style>


