<template>
  <div class="user-info" slot="other">
    <Dropdown @on-click="handleClick">
      <a href="javascript:void(0)">{{userName}}<span class="user-photo"><Icon type="person"></Icon></span></a>
      <DropdownMenu slot="list">
        <DropdownItem name="userAccount"><Icon type="android-person"></Icon>帐号信息</DropdownItem>
        <DropdownItem name="combo"><Icon type="pie-graph"></Icon>套餐使用</DropdownItem>
        <DropdownItem name="exit"><Icon type="android-exit"></Icon>退出帐号</DropdownItem>
      </DropdownMenu>
    </Dropdown>
  </div>
</template>

<script>
  import Icon from 'iview/src/components/icon'
  import Dropdown from 'iview/src/components/dropdown'
  import Modal from 'iview/src/components/modal'
  import Message from 'iview/src/components/message'

  export default {
    name: 'user_photo',
    props: {
      userName: String,
      userId: Number
    },
    components: {
      Icon,
      Dropdown,
      DropdownMenu: Dropdown.Menu,
      DropdownItem: Dropdown.Item
    },
    methods: {
      handleClick (name) {
        if (name === 'exit') {
          this.$Modal.confirm({
            title: '确认退出',
            content: '<p>确定退出应用吗？</p>',
            onOk: () => {
              this.$Message.info('Clicked ok')
            },
            onCancel: () => {
              this.$Message.info('Clicked cancel')
            }
          })
          return
        }
        this.$router.push({
          path: '/console/' + name
        })
      }
    },
    mounted () {
      this.$Modal = Modal
      this.$Message = Message
    }
  }
</script>
<style lang="scss">
  .user-info {
    font-size: 14px;
    .user-photo {
      display: inline-block;
      width: 40px;
      height: 40px;
      margin-left: 5px;
      border-radius: 50%;
      vertical-align: middle;
      background: #000;
      text-align: center;
      font-size: 30px;
    }
    .ivu-dropdown-item .ivu-icon {
      margin-right: 5px;
    }
  }
</style>


