<template>
  <!-- 主页内容模块：公共部分为标题和说明 -->
  <section :class="classes">
    <div :class="wrapClasses">
      <div :class="headClasses">
        <div :class="headTitleClasses" v-if="showTitle"><slot name="title"></slot></div>
        <p :class="headDescClasses" v-if="showDesc"><slot name="desc"></slot></p>
      </div>
      <div :class="contentClasses"><slot></slot></div>
    </div>
  </section>
</template>

<script>
  const prefixCls = 'gtu-index-section'
  const wrapClass = 'gtu-container'

  export default {
    name: 'homepage_section',
    data () {
      return {
        showTitle: true,
        showDesc: true
      }
    },
    computed: {
      classes () {
        return [`${prefixCls}`]
      },
      wrapClasses () {
        return [`${wrapClass}`]
      },
      headClasses () {
        return [`${prefixCls}-head`]
      },
      headTitleClasses () {
        return [`${prefixCls}-head-title`]
      },
      headDescClasses () {
        return [`${prefixCls}-head-desc`]
      },
      contentClasses () {
        return [`${prefixCls}-content`]
      }
    },
    mounted () {
      this.showTitle = this.$slots.title !== undefined
      this.showDesc = this.$slots.desc !== undefined
    }
  }
</script>

<style lang="scss">
  $prefixCls : ".gtu-index-section";
  $sectionPadding: 76px;
  $headMarginBottom: 40px;

  #{$prefixCls} {
    padding: $sectionPadding 0;
    font-size: 1.4rem;
    &-head {
      text-align: center;
      margin-bottom: $headMarginBottom;
      &-title {
        font: {
          size: 28px;
          // size: 26px;
          weight: normal;
        }
        margin-bottom: 20px;
        // margin-bottom: 15px;
      }
      &-desc {
        font: {
          size: 24px;
          // size: 16px;
          weight: 300;
        }
      }
    }
  }
</style>


