<template>
  <div :class="classes">
    <iButton v-show="showPrevButton" type="ghost" icon="ios-arrow-thin-left">上一页</iButton>
    <span>第{{current}}页／共{{total}}页</span>
    <iButton v-show="showNextButton" type="ghost" icon="ios-arrow-thin-right">下一页</iButton>
  </div>
</template>

<script>
  import iButton from 'iview/src/components/button'
  import Icon from 'iview/src/components/icon'
  const prefixCls = 'gtu-pagination'

  export default {
    name: 'pagination',
    components: {iButton, Icon},
    props: {
      total: Number,
      current: Number
    },
    data () {
      return {
        showPrevButton: this.current !== 0,
        showNextButton: this.current !== this.total
      }
    },
    computed: {
      classes () {
        return `${prefixCls}`
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : ".gtu-pagination";

  #{$prefixCls} {
    margin: 40px 0;
    display: flex;
    justify-content: space-between;
    align-items: 'center'
  }
</style>


